/*초기화 css*/
@charset "utf-8";

/*웹폰트*/
@import url('https://fonts.googleapis.com/css?family=Montserrat|PT+Serif');

/*css 초기화*/
html, body, div, span, object, iframe, h1,h2,h3,h4,h5,h6,p,blockquote,pre,
abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,
var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,
thead,tr,th,td,article,aside,canvas,details,figcaption,figure,footer,header,
hgroup,menu,nav,section,summary,time,mark,audio,video{
margin:0; padding:0; border:0; outline:0; font-size:100%; vertical-align:baseline;
background:transparent;
}
body{
font-family:'Montserrat', DroidSans, AppleSDGothicNeo, 
Sans-serif, Helvetica;
line-height:1;   
}
article, aside, details, figcaption, figure, footer, header, hgroup,
menu, nav, section{
display:block;
}
ul,ol,li{list-style:none;}
a{
margin:0; padding:0; font-size:100%; vertical-align:baseline;
background:transparent;
text-decoration: none;
}
hr{
display:block; height:2px; border:0; margin:0; padding:0;
}
input, textarea{
width:100%; margin:0; padding:0; border:0;
border-bottom:1px solid #757575;
color:#3151b5;
font-family:'Montserrat';
font-size:12px;
box-sizing:content-box;
appearance:none;
}
::-webkit-input-placeholder, ::-webkit-textarea-placeholder{
color:#3f51b5;
}
::-o-input-placeholder, ::-o-textarea-placeholder{
color:#3f51b5;
}
::-moz-input-placeholder, ::-moz-textarea-placeholder{
color:#3f51b5;
}
input-placeholder, textarea-placeholder{
    color:#3f51b5;    
}
/*여러브라우저 설정에 맞게 체크하는걸 크로스 브라우징*/
